<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      count: 0,
      closeCount: 0
    }
  },
  created(){},
  mounted() {
    this.$global.appendChild()
    this.handleState()
  },
  methods: {
    handleState(){
      var ths = this

      history.replaceState(null, null, '/ad/ad1');
      history.pushState( null, null, '#/ad/ad1');
      
      window.addEventListener('popstate', function(event) {
        console.log("addEventListener:"+ths.count)
        ths.count = ths.count + 1
        ths.closeCount = ths.closeCount + 1

        switch(ths.count) {
          case 1:
            history.pushState( null, null, '#/ad/ad2');
            break;
          case 2:
            history.pushState( null, null, '#/ad/ad3');
            break;
          case 3:
            history.pushState( null, null, '#/ad/ad4');
            break;
          case 4:
            history.pushState( null, null, '#/ad/ad5');
            break;
          case 5:
            history.pushState( null, null, '#/ad/ad1');
            ths.count = 0
            break;         
        }
        if(ths.closeCount == 10){
          WeixinJSBridge.call('closeWindow');
        }
        console.log("after:"+ths.count)
      })
    }
  },
  watch: {
    $route() {
      this.$global.routeWatch()
    }
  }
}
</script>

<style>
@import './assets/css/reset.css';
html {
  overflow: hidden;
}
#app{
  font-size: 17px;
  height: 100%;
  background: #eee;
}
@font-face {
  font-family: 'iconfont';
  src: url('./assets/css/font/iconfont.eot');
  src: url('./assets/css/font/iconfont.eot?#iefix') format('embedded-opentype'),
      url('./assets/css/font/iconfont.woff2') format('woff2'),
      url('./assets/css/font/iconfont.woff') format('woff'),
      url('./assets/css/font/iconfont.ttf') format('truetype'),
      url('./assets/css/font/iconfont.svg#iconfont') format('svg');
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



