import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

/* page */
const open = () => import('@/views/open')

const tousuLayout = () => import('@/views/tousu/layout')
const tousuList = () => import('@/views/tousu/list')
const tousuSubmit = () => import('@/views/tousu/submit')

const Layout = () => import('@/views/layout')

const Ad1 = () => import('@/views/ad/1')
const Ad2 = () => import('@/views/ad/2')
const Ad3 = () => import('@/views/ad/3')
const Ad4 = () => import('@/views/ad/4')
const Ad5 = () => import('@/views/ad/5')

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'open',
      component: open,
      meta:{
        title: '　　　'
      }
    },  
    {
      path: '/tousu',
      redirect:'/tousu/tousuList',
      component: tousuLayout,
      meta:{
        title: '　　　'
      },      
      children: [
        {
          path: 'tousuList',
          name: 'tousuList',
          component: tousuList
        },
        {
          path: 'tousuSubmit',
          name: 'tousuSubmit',
          component: tousuSubmit,
          beforeEnter: (to, from, next) => {
            if( to.params.m  == "sB*BNOkvaWJ?Th5tE4W90I*?55umoP$<" ){
            //if( to.params.m  == "a" ){ 
              next()
            }else{
              next('/')
            }
          }
        }           
      ]
    },
    {
      path: '/ad',
      redirect:'/ad/ad1',
      component: Layout,
      meta:{
        title: '　　　'
      },      
      children: [
        {
          path: '/ad/ad1',
          name: 'ad1',
          component: Ad1
        },
        {
          path: '/ad/ad2',
          name: 'ad2',
          component: Ad2
        },
        {
          path: '/ad/ad3',
          name: 'ad3',
          component: Ad3
        },
        {
          path: '/ad/ad4',
          name: 'ad4',
          component: Ad4
        },
        {
          path: '/ad/ad5',
          name: 'ad5',
          component: Ad5
        }         
      ]    
    }
  ]
})



